.nav-menu{
    display: flex;
}

.nav-menu li{
    padding: 0 1rem;
 }

 .nav-menu a{
    font-size: 1.5rem;
    font-weight: 500;
 }

 .header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    font-size: 2rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index : 10;
 }

 .header-bg{
    background-color: rgba(0,0,0,0.85);
    transition: 0.5s;
 }

 .hamburger{
    display: none;
 }
  @media screen and (max-width: 1040px){ 
    .nav-menu{
        flex-direction:column;
        justify-content:center;
        align-items:center;
        width: 100%;
        height: 100vh;
        background: rgba(0,0,0,0.9);
        position: absolute;
        top: 0;
        left:-100%;
        z-index: -3;
        transition: 0.5s;
        opacity: 0.9;
    }
    .nav-menu.active{
        left: 0;
    }

    .nav-menu li{
        padding: 1rem 0; 

    }

    .nav-menu li a{
        font-size: 2.5rem;
        z-index: 2;
    }

    .hamburger{
        display: initial;
     }
 } 